	module.exports = "<header id=\"main-banner\"><img id=\"main-graphic\" src=\"{{mainBackgroundImg}}\" alt=\"droidcon Kraków main graphic\"/><div id=\"conference-timer\"><div class=\"clock-builder-output\"><div class=\"countdown-fld\"><span>Days</span><div id=\"days\"></div></div><div class=\"countdown-fld\"><span>Hours</span><div id=\"hours\"></div></div><div class=\"countdown-fld\"><span>Minutes</span><div id=\"minutes\"></div></div><div class=\"countdown-fld\"><span>Seconds</span><div id=\"seconds\"></div></div></div></div><div id=\"main-buttons\"><a class=\"green-button\" href=\"https://goo.gl/eyw7uF\" target=\"_blank\">Buy ticket</a><a class=\"green-button\" href=\"https://goo.gl/forms/jRwLWmej0aflhVII2\" target=\"_blank\">Call 4 Papers</a><a class=\"green-button\" href=\"\" target=\"_blank\">Contact</a></div></header><section class=\"white-background\" id=\"about\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-7 col-sm-6 col-lg-4\"><h1 class=\"section-header\">About the conference</h1></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">droidcon Kraków is a part of global conference\nseries and a network focusing on the best of Android.<br/>Be a part of it!<br/>Join 3rd edition of droidcon Kraków!</p></div></div><div class=\"row\"><div class=\"col-xs-12 col-md-6\"><div class=\"when-where\"><img src=\"{{calendarIcon}}\" alt=\"Calendar icon image\"/><p>8-9th December 2016 - Conference<br/>10th December 2016 - Hackathon</p></div></div><div class=\"col-xs-12 col-md-6\"><div class=\"when-where empty\"><img src=\"{{pinIcon}}\" alt=\"Venue icon image\"/><p>ParkInn by Radisson<br/>Monte Cassino 2 St., Kraków</p></div></div></div></div></section><section class=\"light-background\" id=\"recaption\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-7 col-sm-6 col-lg-4\"><h1 class=\"section-header\"> How was it last year?</h1></div></div><div class=\"row\"><div class=\"col-xs-12\"><iframe id=\"youtube-iframe\" width=\"320\" height=\"215\" src=\"https://www.youtube.com/embed/Dg-dbrP9PaM\"></iframe></div></div><div class=\"row\"><div class=\"col-xs-12\"><div class=\"section-paragraph\"><a class=\"green-button\" href=\"https://www.youtube.com/watch?v=Dg-dbrP9PaM&amp;list=PLvpKlOXx1MBMfL4llJOi0VX6LcQVP0Su0\" target=\"_blank\">See more</a></div></div></div></div></section><section class=\"green-background\" id=\"participate\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-7 col-sm-6 col-lg-4\"><h1 class=\"section-header\">Participate in 2016!</h1></div></div><div class=\"row\"><div class=\"col-xs-12 col-lg-4\"><img src=\"{{learnIcon}}\" alt=\"Learn icon\"/><h4 class=\"section-subtitle\">Come as attendee</h4><p class=\"section-paragraph\">New place, more new speakers,\ntwo days full of anroid talks\nand workshops on day three!\nYou simply must be there this year!</p></div><div class=\"col-xs-12 col-lg-4\"><img src=\"{{teachIcon}}\" alt=\"Teach icon\"/><h4 class=\"section-subtitle\">Become a speaker</h4><p class=\"section-paragraph\">Have knowledge about Android technology\nand want to share it with others?\nNothing more simple!\nSubmit your paper and become a speaker at\ndroidcon Kraków 2016!</p></div><div class=\"col-xs-12 col-lg-4\"><img src=\"{{supportIcon}}\" alt=\"Support icon\"/><h4 class=\"section-subtitle\">Support us</h4><p class=\"section-paragraph\">Help us make the conference a better event\nsimply by donating, volunteering or supporting us in\nsocial media!\nContact us for more information!</p></div></div></div></section><speakers></speakers><section class=\"grey-background\" id=\"contact\"><div class=\"container\"><div class=\"row\"><div class=\"col-xs-7 col-sm-6 col-lg-4\"><h1 class=\"section-header\"> Contact us</h1></div></div><div class=\"row\"><div class=\"col-xs-12\"><p class=\"section-paragraph\">If you want to know more or have any questions regarding droidcon\nKraków, don't hesitate to email us!</p></div><div class=\"col-xs-12\"><p class=\"section-paragraph\"><a class=\"green-button\" href=\"mailto:contact@droidcon.pl\" target=\"_blank\">contact@droidcon.pl</a></p></div></div></div></section><app-footer></app-footer>";

